@import url('https://fonts.googleapis.com/css?family=Ubuntu:400,700&display=swap');

html{

    margin: 0;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    background-color:	#DCDCDC !important;
    /* padding: 15px; */
    
}

h1{
    display: block;
    font-family: Ubuntu;
    font-weight: 700;
    font-size: 30px;
    color: #190052 !important;
    line-height: 1.2;
    text-transform: uppercase;
    text-align: center;
    
    margin-top: 5rem !important;
   
    
    
}

.wrap{
   
    width: 50%;
    font-size: 1rem;
    line-height: 1.5;
    /* color: #212529; */
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
     align-content: center;
     margin-left: auto;
     margin-right: auto;
}

input{
    outline: none;
    border: none;
    margin: 1rem;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    
    
}


   
.input100{
    height: 2.5rem;
    padding: 0 2rem ;
    box-sizing: border-box;

    display: block;
    /* width: 100%; */
    background: #fff;
    border-radius: 31px;
    font-family: Ubuntu;
    font-size: 1.1rem;
    font-weight: 600;
    line-height: 1.2;
    
}





.btn{
    outline: none;
    border: none;
    color: #ffffff;
    font-weight: 600;
    letter-spacing: 0.2rem;
    text-shadow: none;
    display: block;
    text-align: center;
    width: 90%;
    cursor: pointer;
    background-color: #32116A;
    box-sizing: border-box;
    margin:5% auto;
    padding: 1rem;
    border-radius: 31px;
}

.btn:hover{
    background-color:  #4B00A9;

}

p{
    display: block;
    font-family: Ubuntu;
    font-weight: 700;
    font-size: 1rem !important;
    
    line-height: 1.2;
   
    text-align: center;
    margin: 1rem;
}

.errors{
    display: block;
    font-family: Ubuntu;
    line-height: 0.5;
   
    text-align: left;
    margin: 1rem;
    color: red;
    font-size: 0.9rem !important;
    font-weight: lighter !important;
}


.contain{
    display: flex;
    align-items: baseline;
    justify-content: space-between;   
}

.dropdown{
    height: 2.5rem;
    padding: 0 2rem ;
    /* box-sizing: border-box; */

    /* display: block; */
    /* width: 100%; */
    background: #fff;
    border-radius: 31px;
    font-family: Ubuntu;
    /* font-size: 1.1rem; */
    font-weight: 600;
    line-height: 1.2;
    /* outline: none; */
    /* width: 80%; */
    border: none;

    
}