@import url('https://fonts.googleapis.com/css?family=Ubuntu:400,700&display=swap');

.mainCard{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: flex-start;
    /* align-content:space-between; */
}

.card{
    border: 1px solid #212529;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 2rem;
    justify-content: flex-start;
    align-content: flex-start;

    width: 25%;
}

p{
    font-family: Ubuntu;
    font-size: 1.1rem;
    color: #000000;
    font-weight: 500;
}

h1{
    display: block;
    font-family: Ubuntu;
    font-weight: 700;
    font-size: 30px;
    color: #403866;
    line-height: 1.2;
    text-transform: uppercase;
    text-align: center;
    margin: 1rem;
    /* padding-bottom: 49px; */
}

.showBtn{
    outline: none;
    border: none;
    color: #ffffff;
    font-weight: 600;
    letter-spacing: 0.2rem;
    text-shadow: none;
    /* display: block; */
    text-align: center;
    width: 100%;
    cursor: pointer;
    background-color: #32116A;
    box-sizing: border-box;
    margin: 0 !important;
    padding: 1rem;
    /* border-radius: 31px; */
}