.model{
    position: fixed;
    z-index: 500;
    background-color: white;
    width: 60%;
    box-shadow: 1px 1px 1px black;
    padding: 16px;
    left: 20%;
    top:30%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
}

p{
    /* font-weight: 600 !important; */
    letter-spacing: 0.1rem;   
}

.wrap{
    
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
     align-content: center;
     margin-left: auto;
     margin-right: auto;
}

.btn{
    outline: none;
    border: none;
    color: #ffffff;
    font-weight: 600;
    letter-spacing: 0.2rem;
    text-shadow: none;
    display: block;
    text-align: center;
    width: 20%;
    cursor: pointer;
    background-color: #32116A;
    box-sizing: border-box;
    margin: 1rem;
    padding: 1rem;
    border-radius: 31px;
}

.btn:hover{
    background-color:  #4B00A9;

}