.full{
  
    background-color: #d3d3d3;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    
    /* width: 80%; */
    
    /* box-shadow: 1px 1px 1px black; */
    padding: 2rem;
  
    box-sizing: border-box;
    /* backface-visibility: hidden; */
    /* overflow: scroll; */

    /* position: fixed;
    top: 15%;
    left: 10%;
    right: 0;
    bottom: 0;
    z-index: 300; */

    transition: all 0.3s ease-out;
}

.input{
    outline: none;
    border: none;
    margin: 1rem;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    
    
}

label{
    font-family: Ubuntu;
    font-size: 1.1rem;
    font-weight: 600;
    line-height: 1.2;
    float: left;
}
p{

    letter-spacing: 0.1rem;   
}

.download{
    cursor: pointer;
    text-decoration: underline;
}
.buttons{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.Btn{
    outline: none;
    border: none;
    color: #ffffff;
    font-weight: 600;
    letter-spacing: 0.2rem;
    text-shadow: none;
    display: inline;
    text-align: center;
    /* width: 30%; */
    cursor: pointer;
    background-color: #32116A;
    box-sizing: border-box;
    /* left: 40%; */
    /* margin:10% 0%; */
    /* padding: 30px; */
    padding: 1rem;
    border-radius: 31px;
}

.Btn:hover{
    background-color:  #4B00A9;

}

