.splitter{
    margin: 6rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;

}


.btn{
    outline: none;
    border: none;
    color: #ffffff;
    font-weight: 600;
    letter-spacing: 0.2rem;
    text-shadow: none;
    display: block;
    text-align: center;
    width: 20%;
    cursor: pointer;
    background-color: #32116A;
    box-sizing: border-box;
    margin: auto;
    padding: 1rem;
    border-radius: 31px;
}

.btn:hover{
    background-color:  #4B00A9;

}