@import url('https://fonts.googleapis.com/css?family=Ubuntu:400,700&display=swap');

html{
    margin: 0;
    background-color: #f2f2f2;
    /* padding: 15px; */
    
}
.container{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    align-content: space-between;
    flex-wrap: wrap;
}


img{
    display: block;
  
    width: 50%;
}


#info{
    font-size: 2rem;
    font-weight: 400;
    line-height: 1.5;
    text-align: start;
}

span{
    font-size: 5rem;
    font-weight: 700;

}

.btn{
    outline: none;
    border: none;
    font-size: 0.9rem;
    color: #ffffff;
    font-weight: 700;
    letter-spacing: 0.2rem;
    text-shadow: none;
    display: block;
    text-align: center;
    width: 40%;
    cursor: pointer;
    background-color: #32116A;
    box-sizing: border-box;
    margin: 1rem;
    padding: 1rem;
    border-radius: 31px;
}

.btn:hover{
    background-color:  #4B00A9;

}