@import url('https://fonts.googleapis.com/css?family=Montserrat:400,500&display=swap');


* {
        font-family: "Montserrat", sans-serif;

      }
      
      .header {
        display: flex;
        justify-content: flex-start;

        align-items: center;

        background-color: #32116A;
      }
        
      
      .nava{
        font-family: Montserrat;
        font-weight: 500;
        color: white;
        text-decoration: none;
        letter-spacing: 0.15rem;
        text-shadow: none;

      }

      .nava:hover{
        text-decoration: underline;
        color: white;
      }
      
      .nav__links {
        list-style: none;
        display: flex;
        font-size: 16px;
      }
      
      .nav__links li {
        padding: 0px 20px;
      }
      
      .nav__links li:nth-child(1) {
        padding: 0 20px 0 0;
      }
      
      .nava {
        transition: all 0.3s ease 0s;
      }

      
     .loginfo{

       font-family: Montserrat;
       font-weight: 200;
       color: white;
       text-decoration: none;
       letter-spacing: 0.10rem;
       text-shadow: none;
       padding: 1rem;
     }

     .logo {
      content: url('ehrLogo.png');
      width: 5%;
      cursor: pointer;
      margin-right: 2rem;
      padding: 1rem;
      
    }
      
      
     .loglogo{
       display: flex;
       justify-content: flex-end;

       align-items: center;
    

       
     }
     

.containerLogBtn{
  background-color: #ffffff;
  visibility: hidden;
padding: 5px;
border-radius: 6px;

margin: 0.5rem 0 0 6rem;


position: absolute;
z-index: 1;


opacity: 0;
transition: opacity 0.3s;
}


 .logbtn{
     background-color: #32116A;
     cursor: pointer;

  color: #ffffff;
  text-align: center;
  padding: 0.5rem;
  border-radius: 6px;
   } 

   .loginfo:hover .containerLogBtn{
    visibility: visible;
    opacity: 1;
   }


   .containerLogBtn::after {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 50%;

    border-width: 0.5rem;
    border-style: solid;
    border-color: transparent transparent #ffffff transparent;
  }

  .spanN { 
    font-size: 1rem !important;
    font-weight: 500;
  }